
  .q-tabs
    color: #B2BBBB
    height: 80px
  .q-tab
    margin: 0
    padding: 0
  .q-tab--active
    color: #1A4B9A
  .q-tab__content
      flex-direction: column
      flex-wrap: nowrap
  .q-tab__content
    .q-tab__label
      font-size: .6em
  .q-header
    width: 100vw
    height: 229px
    z-index: 1
    background: url('../../public/img/BG-1.png') 0% 5% no-repeat padding-box
    background-color: #0053ab
    opacity: 1
  .q-page-container
    position: absolute /**Por enquanto */
    width: 100%
    height: auto
    top: 152px
    border-radius: 20px 0 0 0
    z-index: 100
    overflow: hidden
  .pic
    position: absolute
    top: 35px
    left: 30px
